form {
  width: 340px;
  margin: 0 auto;
  text-align:center;
}
button, input{
  width: 100%;
  line-height: 54px;
  margin-bottom:10px;
  display: inline-block;
  font-size: 16px;
  font-weight: 500;
}
legend{
  font-weight: 800;
  font-size: 20px;
  line-height: 42px;
  margin-bottom: 20px;
}
p{
  display: flex;
  justify-content: flex-end;
  margin-top: 10px;
  font-size: 13px;
}
button:first-of-type{
  display: block;
  padding: 0px 10px;
  text-align: center;
  overflow: hidden;
  width: 100%;
  height: 54px;
  border-radius: 3px;
  color: rgb(255, 255, 255);
  background-color: rgb(95, 0, 128);
  border: 0px none;
}
button:last-of-type{
  display: block;
  padding: 0px 10px;
  text-align: center;
  overflow: hidden;
  width: 100%;
  height: 54px;
  border-radius: 3px;
  color: rgb(95, 0, 128);
  background-color: rgb(255, 255, 255);
  border: 1px solid rgb(95, 0, 128);
}