@charset "UTF-8";
*{padding:0;margin:0;}
ul,li,ol{
  list-style: none;
}
a{
  text-decoration: none;
  color: #333;
}
h2{
  text-align: center;
}
h2, p{
  margin-bottom: 20px;
}
ul{
  margin: 0 auto;
  display: flex;
  /* flex-wrap: wrap; */
  gap: 20px;
  width: 1420px;
}
li{
  width: 25%;
}
li>a>p{
  width: 100%;
  position: relative;
}
li>a>p:first-child{
  height: 437.672px;
  overflow: hidden;
}
li>a>p>img:first-child{
  z-index: 3;
  position: absolute;
  right: 20px;
  bottom: 20px;
}
li>a>p>img:last-child{
  transition: .5s;
  width: 100%;
}
li>a>p>img:last-child:hover{
  transform: scale(1.03);
}
li>a>p:nth-child(2){
  font-size: 20px;
}
li>a>p:nth-child(3){
  font-weight: bold;
  font-size: 18px;
}
li>a>p:last-child{
  font-size: 14px;
  color: #666 !important;
  /* font-weight: bold; */
}