.m_c_d{
  position: relative;
  margin: 0 auto;
  width: 1200px;
  display: flex;
  justify-content: center;
}
.m_c_d h3{
  display: none;
}
.m_c_d>div:first-child{
  width: 30%;
  /* float:left; */
}
.m_c_d>div:first-child>img{
  width: 100%;
}
.m_c_d>div:last-child{
  width: 70%;
}
.m_c_d>div:last-child>p:first-of-type{
  font-weight: bold;
  text-align: left;
  font-size: 24px;
  margin-bottom: 40px;
}
.m_c_d>div:last-child>div:nth-of-type(2){
  display: flex;
  justify-content: flex-end;
}
.m_c_d>div:last-child>div:nth-of-type(2)>button{
  cursor: pointer;
  font-size: 16px;
  width:30px !important;
  height: 30px !important;
  line-height: 10px !important;
  border: none;
  background-color: aliceblue;
  color: #333;
}
.m_c_d>div:last-child>div:nth-of-type(2)>input{
  text-align: center;
  width:30px !important;
  height: 30px !important;
  line-height: 10px !important;
  border: none;
  background-color: aliceblue;
}
.m_c_d>div:last-child>div:last-of-type{
  text-align: left;
}
.m_c_d>div:last-child>div:last-of-type>p:last-of-type{
  font-size: 18px;
  font-weight: bold;
}
.m_c_d>div:last-child>div:last-of-type>button{
cursor: pointer;
}

#m_c_d_d>dl{
  line-height: 42px;
  display: flex;
  border-bottom: 1px solid #c5c5c5;
}
#m_c_d_d>dl>dt{
  width: 30%;
}
#m_c_d_d>dl>dd{
  width: 70%;
}